import React, { Component } from 'react';
import { Card, CardBody, CardHeader, Col, Row, Button, Form, Input, FormGroup, Label, Media } from 'reactstrap';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import commonService from '../../../core/services/commonService';

import Loader from '../../Loader/Loader';

class User extends Component {
  constructor( props ){
    super( props );

    this.state = {
      profileInfo: {prescreptions: []},
      loading: false,
      editProfile: false,
      profileId: "",
      profileImage: '',
      previewImage: '',
      documentsImages:[],
      uploadedDocumentsImages: [],
      errors: "",
      formProccessing: false,
      formField: {}
    };
    this.submitHandler = this.submitHandler.bind(this);
  }
  componentDidMount() {
  const { match: { params } } = this.props;
    this.setState( { loading: true}, () => {
      commonService.getAPIWithAccessToken('profile/public/'+params.profileId)
        .then( res => {
          console.log(res);
           
          if ( undefined === res.data.data || !res.data.status ) {
            this.setState( {  loading: false } );
            toast.error(res.data.message);  
            this.props.history.push('/user');  
            return;
          }   

          this.setState({loading:false, profileInfo: res.data.data, formField: res.data.data, uploadedDocumentsImages: res.data.data.prescreptions});     
         
        } )
        .catch( err => {         
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else {
            this.setState( { loading: false } );
            toast.error(err.message);    
          }
        } )
    } )
    
  }

   /* Input Field On changes*/
  changeHandler = event => {
    const name = event.target.name;
    const value = event.target.value;
    const formField = this.state.formField
    formField[name] = value;
    this.setState({ formField: formField });
  };

  handleImageChange = (e) => {
    this.setState({
      profileImage: e.target.files[0]
    })
  };
 
  onDocumentsImageChange = event => {   
    this.setState({
      documentsImages: event.target.files,
    });
  };

  deleteDocumentsImages = (index, docType) => {
    this.setState( { loading: true}, () => {
      commonService.deleteAPIWithAccessToken('profile/documents', {profileId: this.state.profileInfo.profileId, index:index})
        .then( res => {
          console.log(res);
           
          if ( undefined === res.data.data || !res.data.status ) {
            this.setState( {  loading: false } );
            toast.error(res.data.message);  
            this.props.history.push('/user');  
            return;
          }   
          let uploadedDocumentsImages = this.state.uploadedDocumentsImages;
          uploadedDocumentsImages.splice(index, 1);
          this.setState({loading:false, uploadedDocumentsImages: uploadedDocumentsImages});     
         
        } )
        .catch( err => {         
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else {
            this.setState( { loading: false } );
            toast.error(err.message);    
          }
        } )
    } )
  }

  submitHandler (event) {
    event.preventDefault();
    event.target.className += " was-validated";
    if(this.validateForm()){
      return false;
    }
    this.setState( { formProccessing: true}, () => {
      const formInputField = this.state.formField;
      const formData = new FormData();
      formData.append('profileId', this.state.profileInfo.profileId);
      formData.append('firstName', formInputField.firstName);
      formData.append('lastName', formInputField.lastName);
      formData.append('phoneNumber', formInputField.phoneNumber);
      formData.append('description', formInputField.description);
      formData.append('gender', formInputField.gender);
      formData.append('dob', formInputField.dob);
      formData.append('existingAligments', formInputField.existingAligments || "");
      formData.append('currentPhysician', formInputField.currentPhysician);
      formData.append('currentMedications', formInputField.currentMedications);
      formData.append('address', formInputField.address || "");
      formData.append('city', formInputField.city || "");
      formData.append('state', formInputField.state || "");
      formData.append('country', formInputField.country || "");
      formData.append('latitude', formInputField.latitude || "");
      formData.append('longitude', formInputField.longitude || "");

      if(this.state.profileImage !== "")
        formData.append('profileImage', this.state.profileImage);
      
      for(let i =0; i < this.state.documentsImages.length; i++){
        formData.append('documentsImages', this.state.documentsImages[i]);
      }
      
      
      
      
      
      commonService.putAPIWithAccessToken('profile/info', formData)
      .then( res => {
        
        if ( undefined === res.data.data || !res.data.status ) { 
          this.setState( { formProccessing: false} );
          toast.error(res.data.message);
          return;
        } 
        this.props.history.push('/admin/users');
        toast.success(res.data.message);
      } )
      .catch( err => { 
        
        if(err.response !== undefined && err.response.status === 401) {
          localStorage.clear();
          this.props.history.push('/login');
        }else
          this.setState( { formProccessing: false } );
          toast.error(err.message);
      } )
      
    } );
    
  };


  /* Validate Form */
  validateForm() {
    let formValid = false;
    let formField = this.state.formField; 
    let errors = {};
    if(formField.firstName === undefined || formField.firstName.trim() === ""){
      formValid = true;
      errors['firstName'] = "First name field required!";
    }
    
    if(formField.firstName === undefined || formField.firstName.trim() === ""){
      formValid = true;
      errors['firstName'] = "First name field required!";
    }

    if(formField.gender === undefined || formField.gender.trim() === ""){
      formValid = true;
      errors['gender'] = "Gender field required!";
    }

    if(formField.dob === undefined || formField.dob.trim() === ""){
      formValid = true;
      errors['dob'] = "Dob field required!";
    }

    if(formField.currentPhysician === undefined || formField.currentPhysician.trim() === ""){
      formValid = true;
      errors['currentPhysician'] = "Current Physician field required!";
    }

    if(formField.currentMedications === undefined || formField.currentMedications.trim() === ""){
      formValid = true;
      errors['currentMedications'] = "Current Medications field required!";
    }
    

    this.setState({errors:  errors});
    return formValid;
    
  }

  render() {

    const { profileInfo, loading, errors, formProccessing, uploadedDocumentsImages } = this.state; 
    let loaderElement = '';    
    //let changeStatusBtn = "";
    let defaultImagePreview = ''; 

    if(profileInfo.profileImage)
      defaultImagePreview = <div className="previewDefaultImage"><img alt="default" width="100" className="img-fluid img-thumbnail" src={profileInfo.profileImage} /></div>
      
    if(loading)
        loaderElement = <Loader />

    return (
      <div className="animated fadeIn">
        <Row>
          <Col lg={12}>
            <Card className="oep-card">
              <CardHeader className="mainHeading">
                <strong>Paitent Profile Details</strong> 
                {!this.state.editProfile
                  ? <Button color="" className="categoryAdd" type="button" onClick={() => {let profileImage = profileInfo.profileImage || "";
                    let documentsImages = profileInfo.prescreptions || [];this.setState({editProfile: true, documentsImages: documentsImages, profileImage: profileImage, formField: this.state.profileInfo})}}><i className="fa fa-pencil"></i> Edit Paitent Details</Button>
                  : <Button color="" className="categoryAdd" type="button" onClick={() => {this.setState({editProfile: false})}}><i className="fa fa-eye"></i> View Paitent Details</Button> }
              </CardHeader>
              <CardBody className="profileInfo">
                  {loaderElement}
                  <ToastContainer />
                  {!this.state.editProfile ?
                    <Row>
                      <Col md={4}><Label><strong>Name: </strong></Label><span> {`${profileInfo.firstName || ''} ${profileInfo.lastName || ''}`}</span></Col>
                      <Col md={4}><Label><strong>Email: </strong></Label><span> {profileInfo.email || 'N/A' }</span></Col>
                      <Col md={4}><Label><strong>Phone: </strong></Label><span> {profileInfo.phoneNumber || 'N/A'}</span></Col>
                      <Col md={4}><Label><strong>Skype ID: </strong></Label><span> {profileInfo.skype_id || ''}</span></Col>
                      <Col md={4}><Label><strong>Gender: </strong></Label><span> {profileInfo.gender || ''}</span></Col>
                      <Col md={4}><Label><strong>DOB: </strong></Label><span> {profileInfo.dob || ''}</span></Col>
                      <Col md={4}><Label><strong>Existing Alignments: </strong></Label><span> {profileInfo.existingAligments || ''}</span></Col>
                      <Col md={4}><Label><strong>Current Physician: </strong></Label><span> {profileInfo.currentPhysician || ''}</span></Col>
                      <Col md={4}><Label><strong>Current Medications: </strong></Label><span> {profileInfo.currentMedications || ''}</span></Col>
                      <Col md={4}><Label><strong>Address: </strong></Label><span> {profileInfo.address || 'N/A'}</span></Col>
                      <Col md={4}><Label><strong>City: </strong></Label><span> {profileInfo.city || 'N/A'}</span></Col>
                      <Col md={4}><Label><strong>State: </strong></Label><span> {profileInfo.state || 'N/A'}</span></Col>
                      <Col md={4}><Label><strong>Country: </strong></Label><span> {profileInfo.country || 'N/A'}</span></Col>
                      <Col md={4}><Label><strong>Postal Code: </strong></Label><span> {profileInfo.postalCode || 'N/A'}</span></Col>
                      <Col md = {12}>
                        <p><strong>Prescription Doc</strong></p>
                        {
                          profileInfo.prescreptions.length > 0  ? 
                            <Row>
                                { profileInfo.prescreptions.map((prescreptionsInfo, index) =>
                                  <Col md={3} key ={index}>
                                      <div className="prescreptions-preview">
                                        <img src = {prescreptionsInfo} alt = "prescreptions" />
                                      </div>
                                  </Col>  
                                )}
                            </Row>
                          : <></>
                        }
                      </Col>
                    </Row>
                    : 
                    <Row>
                      
                          <Form onSubmit={this.submitHandler} noValidate>
                              <Row>
                                <Col md={"6"}>  
                                  <FormGroup> 
                                    <Label htmlFor="firstName">First Name</Label>            
                                    <Input type="text" placeholder="First Name *" invalid={errors['firstName'] !== undefined && errors['firstName'] !== ""} id="firstName" name="firstName" value={this.state.formField.firstName} onChange={this.changeHandler} required />
                                    <div className="invalid-feedback">
                                      {errors['firstName']}
                                    </div>
                                  </FormGroup>
                                </Col>
                                <Col md={"6"}>  
                                  <FormGroup> 
                                    <Label htmlFor="lastName">Last Name</Label>            
                                    <Input type="text" placeholder="Last Name" id="lastName" name="lastName" value={this.state.formField.lastName} onChange={this.changeHandler}  />
                                    <div className="invalid-feedback">
                                      {errors['lastName']}
                                    </div>
                                  </FormGroup>
                                </Col> 
                                
                                <Col md={"6"}>  
                                  <FormGroup> 
                                    <Label htmlFor="phoneNumber">Contact Number</Label>            
                                    <Input type="text" placeholder="Contact Number " id="phoneNumber" name="phoneNumber" value={this.state.formField.phoneNumber} onChange={this.changeHandler}  />
                                  </FormGroup>
                                </Col>
                                <Col md={"6"}>  
                                  <FormGroup> 
                                    <Label htmlFor="skype_id">Skype ID</Label>            
                                    <Input type="text" placeholder="Skype ID " id="skype_id" name="skype_id" value={this.state.formField.skype_id} onChange={this.changeHandler}  />
                                  </FormGroup>
                                </Col>
                                <Col md={"6"}>  
                                  <FormGroup> 
                                    <Label htmlFor="gender">Gender</Label>            
                                    <Input type="select"  id="gender" name="gender" invalid={errors['gender'] !== undefined && errors['gender'] !== ""} value={this.state.formField.gender} onChange={this.changeHandler}   required >
                                      <option value ="">Select Gender</option>
                                      <option value ="Male">Male</option>
                                      <option value ="Female">Female</option>
                                    </Input>
                                    <div className="invalid-feedback">
                                      {errors['gender']}
                                    </div>
                                  </FormGroup>
                                </Col>
                                <Col md={"6"}>  
                                  <FormGroup> 
                                    <Label htmlFor="dob">DOB</Label>            
                                    <Input type="date" pattern="\d{4}-\d{2}-\d{2}" invalid={errors['dob'] !== undefined && errors['dob'] !== ""} id="dob" name="dob" value={this.state.formField.dob} onChange={this.changeHandler}   required />
                                    <div className="invalid-feedback">
                                      {errors['dob']}
                                    </div>
                                  </FormGroup>
                                </Col>
                                <Col md={"6"}>  
                                  <FormGroup> 
                                    <Label htmlFor="existingAligments">Existing Alignments</Label>            
                                    <Input type="text" placeholder="Existing Alignments " id="existingAligments" name="existingAligments" value={this.state.formField.existingAligments} onChange={this.changeHandler}   required />
                                  </FormGroup>
                                </Col>
                                <Col md={"6"}>  
                                  <FormGroup> 
                                    <Label htmlFor="currentPhysician">Current Physician</Label>            
                                    <Input type="text" placeholder="Current Physician " invalid={errors['currentPhysician'] !== undefined && errors['currentPhysician'] !== ""} id="currentPhysician" name="currentPhysician" value={this.state.formField.currentPhysician} onChange={this.changeHandler}  required  />
                                    <div className="invalid-feedback">
                                      {errors['currentPhysician']}
                                    </div>
                                  </FormGroup>
                                </Col>
                                <Col md={"6"}>  
                                  <FormGroup> 
                                    <Label htmlFor="currentMedications">Current Medication</Label>            
                                    <Input type="text" placeholder="Current Medication " invalid={errors['currentMedications'] !== undefined && errors['currentMedications'] !== ""} id="currentMedications" name="currentMedications" value={this.state.formField.currentMedications} onChange={this.changeHandler} required   />
                                    <div className="invalid-feedback">
                                      {errors['currentMedications']}
                                    </div>
                                  </FormGroup>
                                </Col>
                                <Col md={"6"}>  
                                  <FormGroup> 
                                    <Label htmlFor="address">Address</Label>            
                                    <Input type="text" placeholder="Address" id="address" name="address" value={this.state.formField.address} onChange={this.changeHandler}  />
                                    {/*         
                                    <AutoCompletePlaces setLatitudeLongitude={this.setLatitudeLongitude} address = {this.state.formField.address} />  */}
                                  </FormGroup>
                                </Col>  
                                <Col md={"6"}>  
                                  <FormGroup> 
                                    <Label htmlFor="city">City</Label>            
                                    <Input type="text" placeholder="City" id="city" name="city" value={this.state.formField.city} onChange={this.changeHandler}  />
                                  </FormGroup>
                                </Col>  
                                <Col md={"6"}>  
                                  <FormGroup> 
                                    <Label htmlFor="state">State</Label>  
                                    <Input type="text" placeholder="State" id="state" name="state" value={this.state.formField.state} onChange={this.changeHandler}  />
                                  </FormGroup>
                                </Col>            
                                <Col md={"6"}>  
                                  <FormGroup> 
                                    <Label htmlFor="country">Country</Label>     
                                    <Input type="text" placeholder="Country" id="country" name="country" value={this.state.formField.country} onChange={this.changeHandler}  />
                                    
                                  </FormGroup>
                                </Col>                
                                
                                
                                <Col md={"6"}>  
                                  <FormGroup> 
                                    <Label htmlFor="postalCode">Postal Code</Label>            
                                    <Input type="text" placeholder="Postal Code" id="postalCode" name="postalCode" value={this.state.formField.postalCode} onChange={this.changeHandler}  />
                                  </FormGroup>
                                </Col>

                                
                                <Col md={"6"}>
                                  <FormGroup> 
                                    <Label htmlFor="defaultImage">Profile Image</Label>            
                                    <Input type="file" id="defaultImage" name="defaultImage" className="form-control"  onChange={this.handleImageChange} />
                                    <small>Image will be shown as a profile image.</small>
                                  </FormGroup>              
                                </Col>
                                <Col md={"6"}>{defaultImagePreview}</Col>
                                
                                <Col md={"6"}>
                                  <FormGroup> 
                                    <Label htmlFor="documents">Prescription</Label>            
                                    <Input type="file" id="documents" name="documents" className="form-control" multiple onChange={this.onDocumentsImageChange} />
                                  </FormGroup> 
                                </Col>
                                <Col md={"6"}>
                                  <div className="previewdocumentsImageArea row">
                                      {uploadedDocumentsImages.map((imagesInfo, index) =>
                                        <Media className="previewdocumentsImage col-md-2" key={index}>
                                          <img className="img-fluid img-thumbnail" width="80" alt="documents" src={imagesInfo} /> <i className="fa fa-times text-danger" onClick={() => {if(window.confirm('Are you sure, you want to delete this prescreptions?')){ this.deleteDocumentsImages(index,'documents') };}}></i>
                                        </Media>
                                      )}
                                  </div>             
                                </Col>
                              
                                <Col lg={12}>
                                  <Button color="primary" disabled={formProccessing} type="submit">Update</Button>
                                  <Button color="secondary" type="button" onClick={() => this.props.history.push('/admin/users')}>Cancel</Button>
                                </Col>
                            </Row>
                          </Form>
                    </Row> }
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    )
  }
}

export default User;
